html {
  font-size: 16px;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Lato', Arial, Helvetica, sans-serif;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  color: #102746;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 40.625rem;
  overflow: hidden;
}

.container {
  width: 95%;
  max-width: 1060px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.tabel {
  border-collapse: collapse;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input {
  outline: none;
}

.section {
  width: 95%;
  max-width: 1262px;
  margin: 0 auto;
}

.container-hero {
  min-height: 100vh;
}

.section-hero {
  background-image: url("../img/bg/Herohero.png");
  background-size: cover;
  background-position: center center;
}

.hero {
  color: #FFFFFF;
  padding-top: 16.4375rem;
}

.hero__title {
  width: 55%;
  font-size: 3.125rem;
  line-height: 3.75rem;
  font-weight: 700;
  margin-bottom: 1.375rem;
}

.hero__text {
  display: block;
  width: 50%;
  line-height: 1.5rem;
  margin-bottom: 3.6875rem;
}

.form {
  display: flex;
  align-items: center;
  padding-bottom: 23.5rem;
  flex-wrap: wrap;
}

.search {
  width: 18.5625rem;
  background-color: transparent;
  border: 0;
  background: url("../img/bg/cirlce.png") left center no-repeat;
  background-size: 1.25rem;
  padding: 1.25rem 1.4375rem 1.25rem 2.6875rem;
  border-bottom: 0.125rem solid #BFC0C5;
  margin-right: 3.8125rem;
  color: #fff;
  font-weight: 400;
  font-size: 1.125rem;
  margin-bottom: 20px;
}

.search::placeholder {
  color: inherit;
}

.submit {
  padding: 0.875rem 2.4375rem;
  background: #1B7DFF;
  border: 0;
  cursor: pointer;
  font-size: 1.5rem;
  line-height: 1.8125rem;
  color: #fff;
  transition: .3s;
  display: block;
}

.submit:hover {
  background: #1454a9;
  color: #e2dede;
}

.header {
  position: fixed;
  z-index: 1000;
  width: 100%;
  padding-bottom: 0.9375rem;
  transition: .4s;
  padding: 0 0.9375rem;
}

.header__wrap {
  max-width: 66.25rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 1.875rem;
  padding-bottom: 0.3125rem;
  align-items: center;
}

.menu__list {
  display: flex;
  font-size: 1.125rem;
  line-height: 1.5rem;
  color: #FFFFFF;
}

.menu__item {
  margin-right: 5.125rem;
  transition: .2s;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item:hover,
.menu__item :focus {
  color: #b3b3b3;
}

.header-active {
  background-color: #0E1D28;
}

.section-benefits {
  box-shadow: 0rem 0.125rem 1.25rem rgba(21, 24, 36, 0.15993);
  position: relative;
  margin-bottom: 8.1875rem;
}

.benefits__title {
  display: block;
  font-size: 2.1875rem;
  line-height: 2.625rem;
  font-weight: 700;
  position: absolute;
  top: 1px;
  left: 0;
  transform: translateY(-100%);
  padding: 5.8125rem 6.25rem 5.5rem 6.25rem;
  background-color: #fff;
  width: 100%;
}

.benefits__desc {
  font-size: 1.125rem;
  line-height: 1.5rem;
  margin-bottom: 1.625rem;
  font-weight: 700;
}

.benefits__list {
  display: flex;
  position: relative;
}

.benefits__item {
  display: flex;
  flex-direction: column;
  width: 18rem;
  min-height: 19.8125rem;
  margin-right: 3.4375rem;
}

.benefits__item:last-child {
  margin-right: 0;
}

.palace-icon {
  width: 3.75rem;
  height: 3.75rem;
  margin-bottom: 2.3125rem;
  fill: #3292fffb;
}

.benefits__text {
  margin-bottom: 2.4375rem;
}

.benefits__link {
  margin-right: 1.625rem;
}

.controls-wrap {
  margin-bottom: 6.9375rem;
}

.benefits__control {
  display: block;
  color: #006DFE;
}

.control-icon {
  fill: #006DFE;
  width: 2.0625rem;
  height: 0.5rem;
}

.section-place {
  margin-bottom: 8.1875rem;
}

.section__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 2.25rem;
}

.section__title {
  font-size: 2.1875rem;
  line-height: 2.625rem;
  position: relative;
  padding-bottom: 0.4375rem;
  margin-bottom: 0;
}

.section__title::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 15.8125rem;
  height: 0.1875rem;
  background-color: #006DFE;
}

.places {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.625rem;
}

.places__item {
  overflow: hidden;
}

.places__item:hover .places-img {
  transform: scale(1.05);
}

.places__item--bg {
  grid-column: span 3;
}

.places__item--sm {
  grid-column: span 2;
}

.places__link {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}

.places__link::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(21, 24, 36, 0.4);
  width: 100%;
  height: 100%;
  z-index: 50;
}

.places-img {
  object-fit: cover;
  width: 100%;
  transition: .15s all;
}

.places-img--bg {
  height: 22.125rem;
}

.places-img--sm {
  height: 17.6875rem;
}

.places-title {
  margin-bottom: 0;
  position: absolute;
  bottom: 1.6875rem;
  left: 1.8125rem;
  z-index: 20;
  font-size: 3.125rem;
  line-height: 3.75rem;
  font-weight: 700;
  color: #fff;
  z-index: 100;
}

.tours {
  margin-bottom: 8.6875rem;
}

.tours__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0 6.75rem;
  margin-bottom: 4.8125rem;
  align-items: center;
}

.tours__img {
  width: 100%;
}

.tours__desc {
  padding-left: 2.9375rem;
}

.tours-title {
  font-size: 1.875rem;
  line-height: 2.5rem;
  margin-bottom: 5.3125rem;
}

.tours-text {
  margin-bottom: 3.625rem;
}

.rating-list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 3.625rem;
}

.rating {
  grid-column: span 2;
}

.section-rating {
  margin-bottom: 9rem;
}

.rating-pic {
  display: block;
  position: relative;
  margin-bottom: 2.8125rem;
  overflow: hidden;
}

.rating-pic:hover .rating-img {
  transform: scale(1.05);
}

.section__title--reating {
  margin-bottom: 3.375rem;
}

.rating__item {
  display: flex;
  flex-direction: column;
  display: block;
}

.rating-img {
  transition: .15s all;
  width: 100%;
}

.rating-pic__score {
  color: #FFFFFF;
  fill: #fff;
  height: 3.4375rem;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(21, 24, 36, 0.6);
  width: 100%;
  padding: 1rem 1rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.rating-pic__place {
  font-size: 1.125rem;
  line-height: 1.5rem;
}

.place-rating {
  display: flex;
  align-items: center;
}

.heart {
  width: 1rem;
  height: 0.9375rem;
  margin-right: 0.875rem;
}

.comment {
  width: 0.875rem;
  height: 0.875rem;
}

.rating-pic__like {
  margin-right: 0.25rem;
}

.rating-pic__comment {
  margin-right: 0.5625rem;
}

.rating__description {
  display: block;
  margin-bottom: 1.9375rem;
}

.btn {
  color: #FFFFFF;
  line-height: 1.1875rem;
  padding: 0.75rem 1.1875rem;
  background-color: #006DFE;
  display: block;
  width: 7.375rem;
  transition: .3s;
}

.btn:hover {
  background: #1454a9;
}

.section-video {
  max-width: 90rem;
  width: 100%;
  margin: 0 auto;
}

.player {
  display: block;
  position: relative;
}

.player__elem {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}

#video {
  width: 100%;
  height: 100%;
  outline: none;
}

.player__splash {
  position: absolute;
  background: url("../img/video.png") center center/cover no-repeat;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.play {
  display: block;
  width: 7rem;
  height: 7rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.play-icon {
  width: 100%;
  height: 100%;
}

.player-active .player__splash {
  display: none;
}

.player-active .play {
  display: none;
}

.footer {
  background-color: #0E1D28;
  min-height: 23.125rem;
  padding: 0 0.9375rem;
}

.logo-link--footer {
  width: 10.625rem;
  height: 4rem;
  display: block;
  flex: 1;
}

.footer__wrap {
  max-width: 66.25rem;
  margin: 0 auto;
  display: flex;
  padding-top: 7.375rem;
}

.navigation {
  flex: 1;
  line-height: 1.5rem;
  color: #FFFFFF;
  display: flex;
  white-space: nowrap;
}

.site-navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 8.125rem;
}

.site-navigation:last-child {
  margin-right: 0;
}

.site-navigation__item {
  margin-bottom: 0.25rem;
}

.site-navigation__item--bold {
  font-size: 1.125rem;
  font-weight: 700;
}

.site-navigation__link {
  transition: .2s;
}

.site-navigation__link:hover {
  color: #b3b3b3;
}

.hamburger {
  width: 24px;
  height: 20px;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  display: none;
}

.hamburger__plank {
  display: block;
  background: #fff;
  height: 4px;
  border-radius: 10px;
}

.cross-link {
  display: none;
  position: absolute;
  top: 3%;
  right: 4%;
  fill: #fff;
  width: 36px;
  height: 36px;
}

.cross {
  width: 100%;
  height: 100%;
}

.modal {
  display: none;
  padding: 29px;
  text-align: center;
  z-index: 50000;
}

.fancybox-content {
  background: #fff;
  display: inline-block;
  text-align: center !important;
  margin: 0;
  max-width: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding: 30px 70px !important;
  position: relative;
  text-align: left;
  vertical-align: middle;
  background-color: #1B7DFF;
}

.modal__content {
  font-size: 20px;
  color: #fff;
  font-weight: bold;
}

.input-error {
  border-color: red;
}

.error-modal {
  color: red;
}

.fancybox-button {
  color: #fff !important;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 15px;
  }

  .hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .hero__title {
    width: 100%;
    text-align: center;
  }

  .hero__text {
    width: 80%;
    text-align: center;
  }

  .form {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .search {
    margin-bottom: 25px;
    margin-right: 0;
  }

  .menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    z-index: 10000;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
    transition: .2s;
  }

  .menu--active {
    transform: translateX(0);
  }

  .menu__list {
    flex-direction: column;
    text-align: center;
    justify-content: center;
    font-size: 2.8125rem;
    line-height: 1;
  }

  .menu__item {
    margin-right: 0;
    margin-bottom: 1.875rem;
  }

  .benefits__item {
    margin-right: 1.25rem;
  }

  .places__item--bg {
    grid-column: span 6;
  }

  .places__item--sm {
    grid-column: span 6;
  }

  .tours__item {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 0 1.875rem;
    text-align: center;
  }

  .tours__img--card {
    grid-row-start: -1;
  }

  .tours__desc {
    padding-left: 0;
  }

  .tours-title {
    margin-bottom: 1.875rem;
  }

  .tours-text {
    margin-bottom: 20px;
  }

  .rating-list {
    grid-gap: 2.625rem 1.6875rem;
  }

  .rating {
    grid-column: span 3;
  }

  .rating-pic {
    margin-bottom: 1.875rem;
  }

  .rating__description {
    margin-bottom: 1.25rem;
  }

  .logo-link--footer {
    margin-bottom: 1.875rem;
  }

  .footer__wrap {
    flex-direction: column;
    align-items: center;
    padding-top: 4.375rem;
  }

  .site-navigation {
    margin-right: 5rem;
  }

  .hamburger {
    display: inline-flex;
  }

  .cross-link {
    display: block;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 14px;
  }

  .hero {
    padding-top: 9.375rem;
  }

  .hero__title {
    margin-bottom: 3.125rem;
  }

  .benefits__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .benefits__item {
    margin-right: 0;
    width: 90%;
    min-height: 15rem;
    margin-bottom: 1.5625rem;
  }

  .benefits__item:last-child {
    margin-bottom: 0;
  }

  .palace-icon {
    margin-bottom: 0.9375rem;
  }

  .benefits__text {
    margin-bottom: 0.9375rem;
  }

  .controls-wrap {
    margin-bottom: 0;
  }

  .section__header {
    flex-direction: column;
  }

  .section__title {
    margin-bottom: 0.9375rem;
  }

  .rating {
    grid-column: span 6;
  }

  .footer__wrap {
    padding-top: 3.125rem;
  }

  .navigation {
    flex-wrap: wrap;
  }

  .site-navigation {
    margin-bottom: 20px;
  }
}